.details-page {
    .details-page-wrapper {
        justify-content: space-between;
        color: rgb(85, 85, 85);

        .gig-details {
            margin-top: 20px;
            width: 66%;
            padding: 1rem;
            background-color: #fff;
            gap: 100px;
            .gig-title {
                h2 {
                    font-size: xx-large;
                    margin-top: 20px;
                    margin-bottom: 30px;
                    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
                        "Open Sans", "Helvetica Neue", sans-serif;
                }
                .mini-title {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    img {
                        margin-top: 8px;
                        border-radius: 50%;
                    }
                    h5 {
                        margin-left: 10px;
                        font-size: medium;
                        margin-right: 10px;
                        margin-top: 20px;
                        margin-bottom: 40px;
                        font-family: Macan, Helvetica Neue, Helvetica, Arial, sans-serif;
                        .rate {
                            color: #ffbe5b;
                        }
                        span {
                            color: rgb(146, 146, 146);
                            margin-right: 5px;
                        }
                    }
                }
            }

            .gig-image-slider {
                object-fit: cover;
                .indicator {
                    cursor: pointer;
                    text-align: center;
                }

                .indicator.active {
                    color: #fff;
                    background: #666;
                }
            }
            .delete-gig {
                background-color: rgb(245, 89, 89);
            }
            .delete-gig:hover {
                background-color: red;
            }
            .edit-gig {
                background-color: rgb(113, 203, 238);
            }
            .edit-gig:hover {
                background-color: rgb(33, 174, 230);
            }

            .about-gig {
                margin-top: 40px;

                p {
                    font-family: -webkit-pictograph;
                    font-weight: 600;
                    color: #858585;
                }
            }
            .about-seller {
                margin-top: 40px;
                p {
                    font-size: large;
                    font-weight: 300;
                }
                .seller-profile {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    margin-top: 20px;

                    .seller-img {
                        max-width: 150px;
                        img {
                            border-radius: 50%;
                        }
                    }
                    .seller-sum {
                        margin-top: 10px;
                        margin-left: 20px;
                        .seller-rate {
                            color: #ffbe5b;
                            font-weight: 600;
                        }
                        .contact-me {
                            margin: 20px 0px;
                            button {
                                font-weight: 600;
                                font-size: large;
                                width: 100%;
                                border: 1px solid;
                                border-radius: 4px;
                                text-align: center;
                                transition: 0.4s;
                                padding: 10px 20px;
                            }
                            button:hover {
                                background-color: #858585;
                                color: white;
                            }
                        }
                    }
                }
                .seller-bio {
                    font-family: Arial, Helvetica, sans-serif;
                    border: 1px solid #dadbdd;
                    border-radius: 3px;
                    padding: 5px;
                    ul {
                        height: 150px;
                        column-count: 2;
                        margin: 10px;
                        list-style-position: inside;
                        border-bottom: 1px solid #dadbdd;
                        li {
                            padding: 10px;
                            h6 {
                                font-size: medium;
                                color: #949494;
                                font-weight: 549;
                            }
                            h4 {
                                color: rgb(105, 105, 105);
                                font-size: larger;
                                font-family: Arial, Helvetica, sans-serif;
                            }
                        }
                    }
                    p {
                        margin-top: 20px;
                        margin-left: 20px;
                    }
                }
            }
            .seller-reviews {
                margin-top: 60px;
                font-family: -webkit-pictograph;
                font-weight: 600;

                .react-slideshow-container {
                    margin-top: 40px;
                    width: 100%;
                    margin-bottom: 60px;
                    .images-wrap {
                        width: 70%;
                        .review-slider {
                            margin-left: 5%;
                            margin-right: 5%;
                            display: flex;
                            color: #858585;
                            .seller-image {
                                max-width: 20%;
                                margin-right: 20px;
                                img {
                                    flex: 20%;
                                    height: 70px;
                                    width: 70px;
                                }
                                .seller-rev {
                                    flex: 80%;
                                }
                            }
                        }
                    }
                }
            }
        }

        .sidebar {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: -webkit-sticky;
            position: sticky;
            top: 125px;
            width: 25%;
            max-height: 60vh;
            gap: 100px;
            margin-top: 50px;
            margin-bottom: 10px;
            padding: 1rem;
            background-color: #fff;
            border: 1px solid #cacaca;

            .package-content {
                p {
                    font-size: large;
                    font-family: sans-serif;
                }

                .package-price {
                    margin-bottom: 10%;
                    display: flex;

                    h3 {
                        color: rgb(36, 179, 112);
                        flex: 5;
                        font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
                        span {
                            margin-left: 66px;
                        }
                    }
                }

                .delivery {
                    font-weight: 600;
                    margin-top: 30px;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    justify-content: space-between;
                }

                .check-mark {
                    margin-top: 30px;
                    span {
                        margin-left: 10px;
                    }
                }
            }
            .continue-button button {
                background-color: rgb(36, 179, 112);
                color: rgb(255, 255, 255);
                width: 100%;
                display: flex;
                justify-content: center;
                padding: 12px 28px;
                border: 1px solid transparent;
                border-radius: 4px;
                font-weight: 600;
                font-size: 16px;
                transition: 0.4s;
            }
            button:hover {
                background-color: #1d8d43;
                color: white;
            }

            // .contact-seller-button {
            //     margin-top: 60px;
            //     background-color: rgb(243, 235, 235);
            //     border: 3px solid rgb(100, 100, 100);
            //     // border-radius: 2%;
            //     color: rgb(100, 100, 100);
            //     // width: 100%;
            //     // display: inline-block;
            //     // box-sizing: border-box;
            //     position: relative;
